<template>
  <div
    class="qr"
    v-lazy:background-image="qrBd"
  >
    <div class="qr-box">
      <van-cell-group>
        <field
          label="学习码"
          v-model="invitationCode"
          placeholder="请输入你的学习码"
        />
      </van-cell-group>
      <div class="qr-box-bt">
        <van-button
          size="large"
          type="primary"
          @click="invitation"
        >浏览课堂</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { Field } from "vant";
export default {
  components: { Field },
  data() {
    return {
      invitationCode: "",
      qrBd:
        "//keyushijian-1257034971.cos.ap-shanghai.myqcloud.com/ft/image/qr-bd.jpg"
    };
  },
  methods: {
    invitation() {
      if (this.invitationCode === "") {
        this.$toast("请输入你的学习码");
      } else if (this.invitationCode.length !== 4) {
        this.$toast("请输入正确的学习码");
      } else {
        this.jump(
          "ClassroomInvitation",
          { code: this.invitationCode },
          {},
          true
        );
      }
    }
  }
};
</script>

<style lang="less" scoped>
.qr {
  width: 100vw;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  background: #5272fb 0 0 no-repeat;
  background-size: 100% auto;
  &-box {
    width: 80vw;
    margin-top: 60vh;
    &-bt {
      padding-top: 20px;
      .van-button--large {
        width: 100%;
        height: 40px;
        line-height: 38px;
        border-radius: 10px;
      }
    }
  }
}
</style>
